.login-container {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.login-form {
	width: 25%;
	background: white;
	padding: 1.5rem;
}

.boxed-form {
    width: 100%;
	margin: 5px 0 20px 0;
    padding: 5px 0;
    border-radius: 2px;
    border: 1px solid #e6e6e6;
}

.boxed-form > label {
	font-family: sans-serif;
	font-weight: bold;
	margin-bottom: 2px;
	font-size: 14px;
	padding: 5px;
}

.boxed-form > input {
	border: 0;
	outline: 0;
	width: 90%;
	padding: 5px;
}

.form-group-default {
    background-color: #fff;
    position: relative;
    padding: 8px 12px 4px;
    overflow: hidden;
    -webkit-transition: background-color .2s ease;
    -o-transition: background-color .2s ease;
    transition: background-color .2s ease;
}

.form-group-default .form-control {
    border: none;
    height: 25px;
    min-height: 25px;
    padding: 0;
    margin-top: -4px;
    background: 0 0;
}

.form-group label:not(.error) {
    letter-spacing: 0.6px;
    font-family: sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 600;
    color: #999999;
}

.form-group-default label {
    margin: 0;
    display: block;
    opacity: 1;
    -webkit-transition: opacity .2s ease;
    -o-transition: opacity .2s ease;
    transition: opacity .2s ease;
}

.login-container-new {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
}

.login-left-column {
    width: 75%;
    height: 100%;
    overflow: hidden;
    background-color: black;
}

.login-img-overlay {
    height: 7%;
    position: absolute;
    top: 8%;
    left: 3%;
    z-index: 999;
}

.copyright-text {
    left: 20px;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    bottom: 10px;
    color: #fff;
    opacity: 0.8;
    font-size: 12px;
}

.login-right-column {
    width: 25%;
    height: 100%;
}

.login-form-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 100%;
}

.login-form {
    width: 100%;
    padding: 40px;
}

.login-error {
	margin-top: 10px;
    color: #e4593d;
}

.dashboard-image {
	width: 100%;
	height: 100%;
}



